    text-decoration: none;
    font-weight: bold;
}

ul.table-of-contents-list li:last-child, ol.table-of-contents-list li:last-child{
    border-bottom: 0px;
}

ul.table-of-contents-list li:before, ol.table-of-contents-list li:before{
    content: '\2022';
    display: inline-block;
    margin: 0 2px 0 4px;
    line-height: 1;
