/* 메뉴 표현을 위한 css*/

/* DL Menu In/Out
=======================================================================================================================================
*/
@-webkit-keyframes MenuAnimOut {
    100% {
        -webkit-transform: translateZ(300px);
        opacity: 0;
    }
}

@-moz-keyframes MenuAnimOut {
    100% {
        -moz-transform: translateZ(300px);
        opacity: 0;
    }
}

@keyframes MenuAnimOut {
    100% {
        transform: translateZ(300px);
        opacity: 0;
    }
}

@-webkit-keyframes MenuAnimIn {
    0% {
        -webkit-transform: translateZ(300px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateZ(0px);
        opacity: 1;
    }
}

@-moz-keyframes MenuAnimIn {
    0% {
        -moz-transform: translateZ(300px);
        opacity: 0;
    }
    100% {
        -moz-transform: translateZ(0px);
        opacity: 1;
    }
}

@keyframes MenuAnimIn {
    0% {
        transform: translateZ(300px);
        opacity: 0;
    }
    100% {
        transform: translateZ(0px);
        opacity: 1;
    }
}

@-webkit-keyframes SubMenuAnimIn {
    0% {
        -webkit-transform: translateZ(-300px);
        opacity: 0;
    }
    100% {
        -webkit-transform: translateZ(0px);
        opacity: 1;
    }
}

@-moz-keyframes SubMenuAnimIn {
    0% {
        -moz-transform: translateZ(-300px);
        opacity: 0;
    }
    100% {
        -moz-transform: translateZ(0px);
        opacity: 1;
    }
}

@keyframes SubMenuAnimIn {
    0% {
        transform: translateZ(-300px);
        opacity: 0;
    }
    100% {
        transform: translateZ(0px);
        opacity: 1;
    }
}

@-webkit-keyframes SubMenuAnimOut {
    0% {
        -webkit-transform: translateZ(0px);
        opacity: 1;
        
    }
    100% {
        -webkit-transform: translateZ(-300px);
        opacity: 0;
    }
}

@-moz-keyframes SubMenuAnimOut {
    0% {
        -moz-transform: translateZ(-0px);
        opacity: 1;
    }
    100% {
        -moz-transform: translateZ(-300px);
        opacity: 0;
    }
}

@keyframes SubMenuAnimOut {
    0% {
        transform: translateZ(-0px);
        opacity: 1;
    }
    100% {
        transform: translateZ(-300px);
        opacity: 0;
    }
}

.dl-menuwrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    -webkit-perspective: 1000px;
    -moz-perspective: 1000px;
    perspective: 1000px;
    -webkit-perspective-origin: 50% 200%;
    -moz-perspective-origin: 50% 200%;
    perspective-origin: 50% 200%;
    /* Hide the inner submenus */
}

@media only screen and (min-width: 48em){
    .dl-menuwrapper {
        position: fixed;
        max-width: 175px;
        top: 25px;
        left: 25px;
    }
}
.dl-menuwrapper.dl-menuopen{
    width: 100%;
    height: 100%;
}
.dl-menuwrapper button {
    top: 0;
    left: 0;
    background: #222222;
    border: none;
    width: 48px;
    height: 45px;
    text-indent: -900em;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    outline: none;
    border-radius: 0 0 3px 0;
    opacity: 0.6;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4);
    transition: 0.4s ease-in-out;
}

.dl-menuwrapper button:hover{
    opacity: 1;
}
@media only screen and (min-width: 48em){
    .dl-menuwrapper button {
        border-radius: 3px;
    }
}

.dl-menuwrapper button:hover,
.dl-menuwrapper button.dl-active,
.dl-menuwrapper ul{
    background: #aaa;
}

.dl-menuwrapper button::after{
    content: '';
    position: absolute;
    width: 68px;
    height: 5px;
    background: #fff;
    top: 10px;
    left: 16%;
    box-shadow: 0 10px 0 #fff, 0 20px 0 #fff;
}

.dl-menuwrapper ul {
    padding: 0;
    list-style: none;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    transform-style: preserve-3d;
}

.dl-menuwrapper li {
    position: relative;
}

.dl-menuwrapper li h4{
    margin: 0;
    padding: 15px 20px 0;
    color: rgba(255, 255, 255, 0.9);
}

.dl-menuwrapper li p{
    margin: 0;
    padding: 15px 20px;
    font-size: 14px;
    font-size: 0.875rem;
    color: rgba(255, 255, 255, 0.8);
    font-weight: 300;
}

.dl-menuwrapper li p a{
    display: inline;
    padding: 0;
    font-size: 14px;
}

.dl-menuwrapper li a{
    display: block;
    position: relative;
    padding: 15px 20px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    color: #fff;
}

.dl-menuwrapper li.dl-back a{
    padding-left: 30px;
    background: rgba(0, 0, 0, 0.0);
}

.dl-menuwrapper li.dl-back:after, .dl-menuwrapper li a:not(:only-child):after{
    position: absolute;
    top: 0;
    line-height: 50px;
    font-family: "fontawesome";
    color: #fff;
    speak-as: none;
    -webkit-font-smoothing: antialiased;
    content: '\f105';
}

.dl-menuwrapper li.dl-back:after{
    left: 10px;
    color: rgba(212, 204, 198, 0.5);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    transform: rotate(180deg);
}

.dl-menuwrapper li > a:after{
    right: 10px;
    color: rgba(0, 0, 0, 0.15);
}

.dl-menuwrapper .dl-menu{
    margin: 5px 0 0 0;
    position: relative;
    width: 100%;
    max-height: 90%;
    overflow-y: auto;
    overflow-x: hidden;
    opacity: 0;
    pointer-events: none;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4);
    -webkit-transform: translateY(10ox);
    -moz-transform: translateY(10ox);
    transform: translateY(10ox);
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: inherit;
}

@media only screen and (min-width: 48em){
    .dl-menuwrapper .dl-menu{
        border-radius: 3px;
    }
}

.dl-menuwrapper .dl-menu.dl-menu-toggle{
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.dl-menuwrapper .dl-menu.dl-menuopen{
    opacity: 1;
    pointer-events: auto;
    background-color: #062c33;
    -webkit-transform: translateY(0px);
    -moz-transform: translateY(0px);
    transform: translateY(0px);
}

.dl-menuwrapper .dl-submenu{
    border-radius: 3px;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.0);
}

.dl-menuwrapper .dl-submenu .btn, .dl-menuwrapper .dl-submenu .read-more-header a{
    margin-bottom: 0;
}

.dl-menuwrapper li .dl-submenu{
    display: none;
}


/*
When a submenu is opend, we will hide all li siblings
For that we give a class to the parent menu called "dl-subview"
We also hide the submenu link.
The opened submenu will get the class "dle-subviewopen"
All this is done for any sub-level being entered.
*/
.dl-menu.dl-subview li, .dl-menu.dl-subview li.dl-subviewopen > a, .dl-menu.dl-subview li.del-subview > a{
    display: none;
    background-color: #062c33;
}

.dl-menu.dl-menu.dl-subview li.dl-subview,
.dl-menu.dl-menu.dl-subview li.dl-subview .dl-submenu,
.dl-menu.dl-menu.dl-subview li.dl-subviewopen,
.dl-menu.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu
.dl-menu.dl-menu.dl-subview li.dl-subviewopen > .dl-submenu > li {
    display: block;
    background-color: #062c33;
}

/* Animation classes ofr moving out and in */
.dl-menu.dl-animate-out {
    -webkit-animation: MenuAnimOut 0.4s ease;
    -moz-animation: MenuAnimOut 0.4s ease;
    animation: MenuAnimOut 0.4s ease;
}


.dl-menu.dl-animate-in {
    -webkit-animation: MenuAnimIn 0.4s ease;
    -moz-animation: MenuAnimIn 0.4s ease;
    animation: MenuAnimIn 0.4s ease;
}

.dl-menuwrapper > .dl-submenu.dl-animate-in {
    -webkit-animation: SubMenuAnimIn 0.4s ease;
    -moz-animation: SubMenuAnimIn 0.4s ease;
    animation: SubMenuAnimIn 0.4s ease;
}

.dl-menuwrapper > .dl-submenu.dl-animate-out {
    -webkit-animation: SubMenuAnimOut 0.4s ease;
    -moz-animation: SubMenuAnimOut 0.4s ease;
    animation: SubMenuAnimOut 0.4s ease;
}

/* No TOuch Fallback*/
.no-touch .dl-menuwrapper li a:hover{
    background: rgba(255, 248, 213, 0.1);
}

/* No JS Fallback*/
.no-js .dl-trigger {
    display: none;
}
.no-js dl-menuwrapper{
    position: inherit;
}

@media only screen and (min-width: 48em){
    .no-js .dl-menuwrapper{
        position: absolute;
    }
}

.no-js .dl-menuwrapper .dl-menu{
    position: relative;
    opacity: 1;
    pointer-events: auto;
    -webkit-transform: none;
    -moz-transform: none;
    transform: none;
}

.no-js .dl-menuwrapper li .dl-submenu{
    display: block;
}

.no-js .dl-menuwrapper li .dl-back{
    display: none;
}

.no-js .dl-menuwrapper li a:not(:only-child){
    background: rgba(0, 0, 0, 0.1);
}

.no-js .dl-menuwrapper li a:not(:only-child)::after{
    content: '';
}

.no-js .dl-menu{
    max-height: 100%;
}
.no-js .dl-menu li {
    display: block;
}

.dl-menuwrapper button:hover,
.dl-menuwrapper button.dl-active,
.dl-menuwrapper ul{
    background: #222222;
}

.dl-menu li {
    display: none;
}

.dl-menuopen li {
    display: block;
}

